import { Fornecedor } from './../../entity/Fornecedor';
import { FornecedorServiceProvider } from './../../providers/fornecedor-service/fornecedor-service';
import { TabsPage } from './../tabs/tabs';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-fornecedor-edit',
  templateUrl: 'fornecedor-edit.html',
  providers: [FornecedorServiceProvider]
})
export class FornecedorEditPage {
  model: Fornecedor;

  constructor(public navCtrl: NavController, public navParams: NavParams, private toast: ToastController, private fornecedorProvider: FornecedorServiceProvider ) {
    if (this.navParams.data.fornecedor) {
      this.model = this.navParams.data.fornecedor;
    } else {
      this.model = new Fornecedor();
    }
  }

  save() {
    this.saveFornecedor().subscribe(
      () => {
        this.toast.create({ message: 'Fornecedor salvo com sucesso.', position: 'botton', duration: 3000 }).present();
        this.navCtrl.setRoot(TabsPage)
        //this.navCtrl.pop();
      },
      (error) => {
        this.toast.create({ message: 'Erro ao salvar o fornecedor. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
      });
  }

  private saveFornecedor() {
    if (this.model.id) {
      return this.fornecedorProvider.update(this.model);
    } else {
      return this.fornecedorProvider.insert(this.model);
    }
  }

}



